<template>
  <div class="page-container">
    <div class="header">
      <a-button 
        type="primary" 
        :icon="h(ArrowLeftOutlined)" 
        @click="goHome"
        class="back-button"
      >
        
      </a-button>
      <h1 class="title"></h1>
    </div>

    <a-card class="data-card">
      <div class="card-title"></div>
      <a-table 
        :columns="columns" 
        :data-source="studentData" 
        :pagination="false"
        :loading="loading"
      />
    </a-card>
  </div>
</template>

<script>
import { ArrowLeftOutlined } from '@ant-design/icons-vue'
import { h } from 'vue'

export default {
  name: 'StudentData',
  components: {
    ArrowLeftOutlined
  },
  data() {
    return {
      h,
      loading: false,
      studentData: [],
      columns: [
        {
          title: '',
          dataIndex: 'name',
          key: 'name'
        },
        {
          title: '',
          dataIndex: 'class',
          key: 'class'
        },
        {
          title: '',
          dataIndex: 'attendance',
          key: 'attendance',
          customRender: ({ text }) => `${text}%`
        },
        {
          title: '',
          dataIndex: 'engagement',
          key: 'engagement',
          customRender: ({ text }) => `${text}%`
        },
        {
          title: '',
          dataIndex: 'performance',
          key: 'performance',
          customRender: ({ text }) => `${text}%`
        },
        {
          title: '',
          dataIndex: 'status',
          key: 'status',
          customRender: ({ text }) => {
            const statusMap = {
              active: '',
              inactive: ''
            }
            return statusMap[text] || text
          }
        }
      ]
    }
  },
  async mounted() {
    await this.loadStudentData()
  },
  methods: {
    goHome() {
      this.$router.push('/')
    },
    async loadStudentData() {
      this.loading = true
      try {
        // 
        this.studentData = Array.from({ length: 20 }, (_, index) => ({
          key: index + 1,
          name: `${index + 1}`,
          class: `${Math.floor(Math.random() * 5) + 1}`,
          attendance: (Math.random() * 20 + 80).toFixed(1),
          engagement: (Math.random() * 25 + 70).toFixed(1),
          performance: (Math.random() * 20 + 75).toFixed(1),
          status: Math.random() > 0.3 ? 'active' : 'inactive'
        }))
      } catch (error) {
        console.error(':', error)
      } finally {
        this.loading = false
      }
    }
  }
}
</script>

